import { Pipe, PipeTransform } from '@angular/core'

@Pipe({
  name: 'formatPin',
  standalone: true,
})
export class FormatPinPipe implements PipeTransform {
  transform(value: string | null | undefined): string {
    if (!value) return ''

    // Supprimer tous les espaces existants et ajouter un espace insécable tous les 2 caractères
    const cleanValue = value.replace(/\s/g, '')
    const withSpaces = cleanValue.replace(/(.{2})/g, '$1\u00A0')
    // Supprimer l'espace à la fin s'il y en a un
    return withSpaces.replace(/\u00A0$/, '')
  }
}
